.App {
  text-align: center;
  max-width: 500px;
  margin: 0px auto 100px auto;
}

.guess {
  background: #eee;
  border-radius: 5px;
  margin: 10px 0px;
  padding: 5px 0px;
}

.guess.incorrect {
  background: #f1c40f;
}

.guess.correct {
  background: #2ecc71;
}

.guesser input[type=text] {
  width: 75%;
  padding: 5px;
  margin-right: 3px;
}

.guesser input[type=button] {
  width: 20%;
  padding: 5px;
}

.guess-container {
  position: relative;
}

.guess-search {
  background: white;
  max-height: 300px;
  overflow: scroll;
  position: absolute;
  bottom: 30px;
  width: 75%;
  border: 1px solid #ccc;
  drop-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}

.guess-search.hidden {
  display: none;
}

.guess-search ul li {
  list-style-type: none;
  padding: 5px;
  margin: 0px;
}

.guess-search ul {
  text-align: left;
  margin: 0px;
  padding: 10px 0px;
}

hr {
  margin: 50px 0px;
}

.paintings {
  height: 480px;
  overflow: scroll;
  margin-top: 15px;
}

.paintings img {
  max-height: 400px;
  max-width: 480px;
  margin: 5px auto;
  display: block;
  border: 2px solid white;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
}

.paintings {
}

.paintings input {
  margin: 3px 3px 15px 3px;
  width: 25px;
  height: 25px;
  text-align: center;
  background: #8e44ad;
  color: white;
  border: none;
  border-radius: 50%;
}

.paintings input.selected {
  background: #a97bbb
}